}
.account-block{
  display: none;
  position: absolute;
  top: 38px;
  right: 15px;
  width: 215px;
  padding: 10px 0 0 0;
  z-index: 10;
}
.account-list{
  background-color: #fff;
  padding: 8px 0;
  box-shadow: 0 0 11px 1px rgba(0, 0, 0, 0.15);
  //position: relative;
  li{
    margin: 8px 0;
    a{
      color: #757575;
      font-size: 16px;
      font-weight: 500;
      display: block;
      padding: 0 30px;
      line-height: 34px;
      &:hover{
        background-color: #e8e8e8;
      }
    }
  }
  &:after{
    width: 0;
    height: 0;
    border-bottom: 7px solid #fff;
    border-left: 7px solid transparent;
    border-right: 7px solid transparent;
    content: '';
    display: inline-block;
    position: absolute;
    top: 3px;
    right: 12px;
    //content: url("~@/assets/img/arrow.png");
  }
}
 

