.ellipsis-gradient {
  white-space: nowrap;
  overflow: hidden;
  background: #fff;
  position: relative;
}
.ellipsis-gradient::after {
  content: '';
  position: absolute;
  right: 0;
  top: 0;
  width: 40px;
  height: 100%;
  background: linear-gradient(to right, rgba(255,255,255, 0.2), #fff 100%);
}





	
