*/
.utv-card {
	background-color: #fff;
  -webkit-box-shadow: 0 3px 4px 0 rgba(0,0,0,0.14), 0 3px 3px -2px rgba(0,0,0,0.12), 0 1px 8px 0 rgba(0,0,0,0.2);
  box-shadow: 0 3px 4px 0 rgba(0,0,0,0.14), 0 3px 3px -2px rgba(0,0,0,0.12), 0 1px 8px 0 rgba(0,0,0,0.2);
}

.utv-card {
	.title{
		font-size: 30px;
	}
	.hero,
	.hero-sub,
	.hero-sub_heading,
	.rating-stats {
		margin-bottom: $gap2;

		& hr {
			margin-bottom: $gap2;
		}
	}

	.hero-sub_heading {
		h2 {
			margin-top: 8px;
		}
	}
	.rating-stats {
		.col-mid {
			margin-top: 20px;
		}
		.icon {
			cursor: pointer;
		}
	}
	.icon-thumbs-up.btn-on, 
	.icon-thumbs-down-hand.btn-on{
		color: $color-darkred;
	} 
	.hero{
  	position: relative;  
	}//hero
	.hero-sub { 
		margin-left: $gap3;
		margin-right: $gap3;
	}

 

	hr {
		margin-left: $gap1-2;
    margin-right: $gap1-2; 
	}

	h2 {
		text-transform: uppercase;
    font-size: 16px;
	}
