$bg-color: #f5f5f5;

.GP-menu {
  position: relative; 
  overflow: hidden;
  // height: 276px;
  background-color: $bg-color;
  box-shadow: 0 1px 6px rgba(0,0,0,0.1);
} 
 
.GP-menu {
	* {
	  box-sizing: border-box;
	  -moz-box-sizing: border-box;
	}
	.show-entries-hover {
	  height: 276px;
	  width: 24px;
	  top: 0;
	  z-index: 3;
	  background: white;
	  position: absolute;
	}
	.show-entries-hover.hidden {
	  display: none;
	}
	.show-entries-hover .hover-arrow {
	  background-image: url("data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAABgAAAAYCAYAAADgdz34AAAAbklEQVRIx2P4//8/Ay0xw6gFg98CbKCjo8MAiA8AcQHVLYAa/gGI/4MsoaoFaIaDaAOqWYBmOAgHgMSpYgEWwxNgFlNsAT7DqWXBBVyGDxkLaBtENI9kPMk0gaoW0Dyj0aWowFLYNYzWB6MWMAAAq1H3+AVacVQAAAAASUVORK5CYII=");
	  background-repeat: no-repeat;
	  position: relative;
	  top: 50%;
	  width: 24px;
	  height: 24px;
	  display: block;
	}
	.play-menu {
	  list-style-type: none;
	  width: 200px;
	  padding: 0;
	  z-index: 4;
	  margin: 0;
	  position: relative;
	  box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.2);
	}
	.play-menu li {
		position: relative;
	  // position: absolute;
	  height: 46px;
	  -webkit-transition: top 500ms, left 500ms, background 0s, opacity 500ms;
	  -moz-transition: top 500ms, left 500ms, background 0s, opacity 500ms;
	  -ms-transition: top 500ms, left 500ms, background 0s, opacity 500ms;
	  -o-transition: top 500ms, left 500ms, background 0s, opacity 500ms;
	  transition: top 500ms, left 500ms, background 0s, opacity 500ms;
	}
	.play-menu li a {
	  text-decoration: none !important;
	}
	.play-menu li .menu-entry-icon {
	  display: inline-block;
	  padding: 10px;
	}
	.play-menu li .menu-entry-text {
	  line-height: 20px;
	  display: block;
	  vertical-align: middle;
	  font-size: 18px;
	  position: absolute;
	  padding: 13px 10px;
	  margin-left: 46px;
	  top: 0;
	  width: 154px;
	  font-weight: 300;
	  font-family: "Roboto", sans-serif;
	  color: #555;
	  background-color: $bg-color;
	}
	.play-menu li:hover * + .menu-entry-text {
	  color: white;
	}
	.play-menu li:hover .store + .menu-entry-text {
	  background: #444;
	}
	.play-menu li:hover .apps + .menu-entry-text {
	  background: #B3C833;
	}
	.play-menu li:hover .movies + .menu-entry-text {
	  background: $color-darkred;
	}
	.play-menu li:hover .music + .menu-entry-text {
	  background: #FB8521;
	}
	.play-menu li:hover .books + .menu-entry-text {
	  background: $color-yellow-green;
	}
	.play-menu li:hover .devices + .menu-entry-text {
	  background: #658092;
	}
	.play-menu li#apps .highlight {
	  color: #879727;
	}
	.play-menu li#movies .highlight {
	  color: #a8372c;
	}
	.play-menu li#music .highlight {
	  color: #db6704;
	}
	.play-menu li#books .highlight {
	  color: #147aaa;
	}
	.play-menu li#devices .highlight {
	  color: #4c606e;
	}
	.play-menu li .menu-entry-icon {
	  width: 46px;
	  height: 46px;
	  background-repeat: no-repeat;
	  background-position: center;
	}
	.play-menu li .menu-entry-icon.store {
	  background-image: url("data:image/png;base64,");
	  background-color: #444;
	}
	.play-menu li .menu-entry-icon {
		font-size: 23px;
    color: #fff; 
	}
	.play-menu li .menu-entry-icon.movies { 
	  // background-image: url("data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAABoAAAAaCAYAAACpSkzOAAAANklEQVRIx2P4//8/Az0ww6hF1LCIVmCALWKAArJNxdQ/GnSjQTcadKNBNxp0wyLoRtsMI9wiAFADCXZf9dlZAAAAAElFTkSuQmCC");
	  background-color: $color-darkred;
	}
	.play-menu li .menu-entry-icon.music {
	  background-image: url("data:image/png;base64,");
	  background-color: #FB8521;
	}
	.play-menu li .menu-entry-icon.books {
	  // background-image: url("data:image/png;base64,");
	  background-color: $color-yellow-green;
	}
	.play-menu li .menu-entry-icon.devices {
	  background-image: url("data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAABoAAAAaCAYAAACpSkzOAAAAa0lEQVRIx2P4//8/Az0wusCC/5SDBAYowGcRVQDRFjGQCciyiBxfjFo0ci0C5TkBAqmQKhYZEKHmA8UWwdQQka9GLRq1aNSiUYtItYjYqpsii4gAB3BZhFyi47PoApEWgUplBShNVulNMwwA8TkerCj0FuMAAAAASUVORK5CYII=");
	  background-color: #658092;
	}
	.play-menu li .menu-entry-icon.apps {
	  background-image: url("data:image/png;base64,");
	  background-color: #B3C833;
	}
	.sub-menu {
	  opacity: 0;
	  padding: 0;
	  position: absolute;
	  top: 52px;
	  left: 24px;
	  list-style-type: none;
	}
	.sub-menu.index {
	  z-index: -1 !important;
	}
	.sub-menu.active li {
	  top: 0px;
	  opacity: 1;
	  -webkit-transition: top 500ms 550ms, opacity 500ms 550ms;
	  -moz-transition: top 500ms 550ms, opacity 500ms 550ms;
	  -ms-transition: top 500ms 550ms, opacity 500ms 550ms;
	  -o-transition: top 500ms 550ms, opacity 500ms 550ms;
	  transition: top 500ms 550ms, opacity 500ms 550ms;
	}
	.sub-menu li {
	  position: relative;
	  height: auto;
	  width: 176px;
	  color: #555;
	  font-weight: 300;
	  font-family: "Roboto", sans-serif;
	  cursor: pointer;
	  top: -7px;
	  opacity: 0;
	}
	.sub-menu li.main-link:hover,
	.sub-menu li.secondary-link:hover {
	  background-color: rgba(0, 0, 0, 0.05);
	}
	.sub-menu li.main-link {
	  line-height: 20px;
	  font-size: 18px;
	  padding: 8px 12px 8px 23px;
	}
	.sub-menu li.main-link.highlight {
	  font-weight: 400;
	}
	.sub-menu li.sub-menu-divider {
	  background: rgba(0, 0, 0, 0.1);
	  height: 1px;
	  margin: 14px;
	  width: 148px;
	  cursor: default;
	}
	.sub-menu li.secondary-link {
	  font-size: 14px;
	  line-height: 18px;
	  font-weight: 400;
	  padding: 6px 12px 6px 23px;
	}
	.GP-hide {
	  opacity: 0;
	  left: -200px;
	}
	.GP-hide a .menu-entry-text {
	  background: white;
	}

}//....